html {
  box-sizing: border-box;
  height: 100%;
  overflow-x: hidden; }

*,
*::before,
*::after {
  box-sizing: inherit; }

img {
  max-width: 100%; }

a {
  text-decoration: none; }

body {
  min-width: 320px;
  font-family: "PT Mono", "Arial", sans-serif; }

button {
  cursor: pointer; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  bottom: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset 0%;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 31px 25px;
  width: 100%;
  max-width: 1460px; }
  @media (max-width: 1023px) {
    .page-header {
      padding: 20px 25px; } }

.page-header__icon--mobile {
  display: none; }
  @media (max-width: 767px) {
    .page-header__icon--mobile {
      display: block; } }

@media (max-width: 767px) {
  .page-header__icon--desktop {
    display: none; } }

.page-header__contacts {
  display: flex; }

.page-header__contact {
  display: flex;
  align-items: center;
  margin-left: 88px;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: #ffffff; }
  @media (max-width: 1023px) {
    .page-header__contact {
      margin-left: 34px; } }

@media (max-width: 1023px) {
  .page-header__icon {
    margin-top: -7px;
    width: 24px;
    height: 24px; } }

.page-header__text {
  margin-left: 17px; }
  @media (max-width: 1023px) {
    .page-header__text {
      display: none; } }

.page-header__button {
  display: inline-block;
  padding: 13px 23px;
  margin-left: 50px;
  width: 180px;
  height: 45px;
  vertical-align: middle;
  text-transform: uppercase;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: -0.05em;
  color: #ffffff;
  border: 1px #ffffff solid;
  border-radius: 5px;
  cursor: pointer; }
  @media (max-width: 1023px) {
    .page-header__button {
      display: none; } }

.page-body {
  display: flex;
  flex-direction: column;
  min-height: 100%; }

.page-main {
  display: flex;
  flex-direction: column;
  flex-grow: 1; }

.promo {
  display: flex;
  margin: -118px 0 auto;
  padding-top: 120px;
  height: 878px;
  background-color: #1b2d37;
  background: url("../img/promo-image-bg@1x.png") top center no-repeat; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .promo {
      background: url("../img/promo-image-bg@2x.png") cover; } }
  @media (max-width: 1023px) {
    .promo {
      height: 685px; } }

.promo__wrapper {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 1440px; }

.page-footer {
  flex-shrink: 0; }
